import React from 'react'
import './style.css'
import { useLanguageStore } from '../../../../../store/LanguageStore';
import { useColourObjectStore } from '../../../../../store/ColourStore';
import { useAuth } from '../../../../../store/authStore';
import { useActiveScreen } from '../../../../../store/ActiveScreen';
import { userActiveSubLevelDetailsStore } from '../../../../../store/ActiveSubLevelDetailsStore';
import { useActivePopup } from '../../../../../store/ActivePopup';
import { sendGamedata } from 'components/Common/core';

const DidyouknowComponent = (props: any) => {
  const { colourObj } = useColourObjectStore();
  const { auth } = useAuth();
  const { setActiveScreen } = useActiveScreen();
  const { ActivePopup, setActivePopup } = useActivePopup();
  const { ActiveSubLevelDetails, loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();
  const { Language, setLanguage } = useLanguageStore();

  return (
    <div className="sublevel_wrapper" id='activity_input_value'>
      <div className="sublevel_subtitle_container">
        <div className='Title_OrangeU' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>
          {(Language?.value === "KANNADA") ? props.TextData[0]?.title : props.sl_title}</div>
        {props.sl_stitle !== undefined && <div className='sublevel_subtitle'>{(Language?.value === "KANNADA") ? props.TextData[0]?.s_title : props.sl_stitle}</div>}
      </div>

      <div className="sublevel_body_container">
        {props.sl_icon !== undefined && <div className='sublevel_icon'>
          <img className='sublevel_logo' src={props?.sl_icon} />
        </div>}
        {props.sl_body !== undefined && <div className='sublevel_bodytext' style={{ color: colourObj.color_m1 }}>
          {(Language?.value === "KANNADA") ? props.TextData[0]?.body : props.sl_body}</div>}

        <div className="button_container">
          <button className="continue_button_Green" style={{ backgroundColor: colourObj.color_green, marginTop: "-5%" }}
            onClick={() => {
              sendGamedata(props?.id, "SUBLEVEL", "", "", "DONE", auth)

              setTimeout(() => {
                props.subleveldata.map((object: any, id: any) => {
                  if (object.id === props.id) {
                    if (props.subleveldata[id + 1]?.id !== undefined || props.subleveldata[id + 1]?.id !== null || props.subleveldata[id + 1]?.id !== '') {
                      loadActiveSubleveldetails(props.subleveldata[id + 1]?.id)
                      localStorage.setItem("ActiveSubLevel", props.subleveldata[id + 1]?.id)
                    } else {
                      setTimeout(() => {
                        sendGamedata((ActiveSubLevelDetails) && ActiveSubLevelDetails?.id, "SUBLEVEL", "", "", "DONE", auth)
                        setActiveScreen("Level")
                        // sendGamedata((ActiveSubLevelDetails) && ActiveSubLevelDetails.id, "SUBLEVEL", "", "", "DONE", auth)
                        // setActiveScreen((screen: any) => {
                        //   return (screen === "SubLevel") ? "Level" : "Home"
                        // })
                      }, 290);
                    }
                    setActivePopup("submit")
                  }
                })
              }, 1500);

              // setTimeout(() => {
              //   props.subleveldata.map((object: any, id: any) => {
              //     if (object?.id === props?.id) {
              //       if (props.subleveldata[id + 1]?.id !== undefined || props.subleveldata[id + 1]?.id !== null || props.subleveldata[id + 1]?.id !== '') {
              //         loadActiveSubleveldetails(props.subleveldata[id + 1]?.id)
              //         localStorage.setItem("ActiveSubLevel", props.subleveldata[id + 1]?.id)
              //         setActivePopup("submit")
              //         // setTimeout(() => {                        
              //         //   ((Number(((ActiveLevelDetails) && ActiveLevelDetails.progress))) === 100) ?
              //         //     setActivePopup("reward")
              //         //     :
              //         //     setActivePopup("submit")
              //         // }, 500);
              //       }
              //     } else {
              //       setTimeout(() => {
              //         sendGamedata((ActiveSubLevelDetails) && ActiveSubLevelDetails.id, "SUBLEVEL", "", "", "DONE", auth)
              //         setActiveScreen("Level")
              //       }, 290);
              //     }
              //   })
              // }, 1500);

            }}>
            <div className="button_text" style={{ color: colourObj.color_t1 }}>{(Language?.value === "KANNADA") ? "" : "DONE"}</div>
          </button>
        </div>

      </div>
    </div>
  )
}

export default DidyouknowComponent

function auth(id: any, arg1: string, arg2: string, arg3: string, arg4: string, auth: any) {
  throw new Error('Function not implemented.');
}
