import React from 'react'
import { useLanguageStore } from '../../../../../store/LanguageStore';
import './style.css'
import { useColourObjectStore } from '../../../../../store/ColourStore'
import Animationcomponent from 'components/Common/Animation';
import { userActiveSubLevelDetailsStore } from '../../../../../store/ActiveSubLevelDetailsStore';
import { userActiveLevelDetailsStore } from '../../../../../store/ActiveLevelDetailsStore';
import { useActiveScreen } from '../../../../../store/ActiveScreen';
import { useActivePopup } from '../../../../../store/ActivePopup';
import { sendGamedata } from 'components/Common/core';
import { useAuth } from '../../../../../store/authStore';

const VideoComponent = (props: any) => {

  const { aniout } = props;

  const { auth } = useAuth();

  const { colourObj, loadcolor } = useColourObjectStore();

  const { Language, setLanguage } = useLanguageStore();

  const { ActivePopup, setActivePopup } = useActivePopup();
  const { setActiveScreen } = useActiveScreen();
  const { ActiveSubLevelDetails, loadActiveSubleveldetails } = userActiveSubLevelDetailsStore();

  return (
    <div className='videoscreen_container'>
      {/* <img className='sublevel_logo' src={props?.sl_icon} /> */}
      <Animationcomponent
        className={'video_text_wrapper'}
        Animationtype={"fade_out"}
        aniout={aniout}
      >
        <div className='video_title'>{(Language?.value === "KANNADA") ? props.TextData[0]?.title : props?.sl_title}</div>
        <div className='video_subtitle'>{(Language?.value === "KANNADA") ? props.TextData[0]?.s_title : props?.sl_stitle}</div>
        <div className='video_body'>{(Language?.value === "KANNADA") ? props.TextData[0]?.body : props?.sl_body}</div>
      </Animationcomponent>

      <Animationcomponent
        className={"videoplayer"}
        Animationtype={"fadezoom_out"}
        aniout={aniout}
      >
        <div className="loader" style={{ borderColor: colourObj.color_m2 }} />
        <iframe className="iframe_videoplayer" src={props?.sl_media} width="100%" height="100%" frameBorder="0"></iframe>
      </Animationcomponent>

      <button className="continue_button_Orange index-helper" style={{ backgroundColor: colourObj.color_m2 }}
        onClick={() => {
          sendGamedata((ActiveSubLevelDetails) && ActiveSubLevelDetails.id, "SUBLEVEL", "", "", "DONE", auth)

          setTimeout(() => {
            props.subleveldata.map((object: any, id: any) => {
              if (object.id === props.id) {
                if (props.subleveldata[id + 1]?.id !== undefined || props.subleveldata[id + 1]?.id !== null || props.subleveldata[id + 1]?.id !== '') {
                  loadActiveSubleveldetails(props.subleveldata[id + 1]?.id)
                  localStorage.setItem("ActiveSubLevel", props.subleveldata[id + 1]?.id)
                } else {
                  setTimeout(() => {
                    sendGamedata((ActiveSubLevelDetails) && ActiveSubLevelDetails?.id, "SUBLEVEL", "", "", "DONE", auth)
                    setActiveScreen("Level")
                  }, 290);
                }
                setActivePopup("submit")
              }
            })
          }, 1500);
        }}>
        <div className="button_text" style={{ color: colourObj.color_t1 }}>SUBMIT</div>
      </button>

    </div>
  )
}

export default VideoComponent